<template>
  <div>
    <table class="table">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in adminorders" :key="item.id">
          <td>{{item.num}}</td>
          <td>
            <span v-if="item.is_paid" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>{{item.total}}</td>
          <td>
            <span>{{date1(item.create_at)}}</span>
            <br>
            <span>{{date2(item.create_at)}}</span>
          </td>
          <td>
            <button
              class="btn btn-sm btn-outline-primary"
              @click="openModal('#OrderDetailModal', 'order', 'ReadOnly', item)"
            ></button>
          </td>
          <td>
            <button
              class="btn btn-sm btn-outline-primary"
              @click="openModal('#OrderUserDetailModal', 'order', 'ReadOnly', item)"
            ></button>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination @updatepage="getAdminOrders"/>
    <Modal/>
  </div>
</template>

<script>
import Pagination from '../components/Pagination.vue';
import Modal from '../components/Modal.vue';

export default {
  methods: {
    getAdminOrders(page) {
      this.$store.dispatch("getAdminOrders", page);
    },
    openModal(modal, species, status, item) {
      this.$store.dispatch("openModal", {modal, species, status, item});
    },
    date1(timestamp) {
      const date = new Date(timestamp * 1000);
      const Month = date.getMonth() + 1;
      const Day = date.getDate();
      const Y = date.getFullYear() + "-";
      const M = Month < 10 ? "0" + Month + "-" : Month + "-";
      const D = Day < 10 ? "0" + Day : Day;
      return Y + M + D;
    },
    date2(timestamp) {
      const date = new Date(timestamp * 1000);
      const Hours = date.getHours();
      const Minutes = date.getMinutes();
      const Seconds = date.getSeconds();
      const H = Hours < 10 ? "0" + Hours : Hours;
      const M = Minutes < 10 ? "0" + Minutes : Minutes;
      const S = Seconds < 10 ? "0" + Seconds : Seconds;
      return `${H}-${M}-${S}`;
    }
  },
  created() {
    this.getAdminOrders();
  },
  computed: {
    adminorders() {
      return this.$store.state.adminorders;
    }
  },
  components: {
      Pagination,
      Modal
  }
};
</script>