<template>
  <div>
    <div class="text-right">
      <button type="button" class="btn btn-primary" @click="openModal('#couponModal', 'coupon', 'New', {})"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th>#</th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,index) in coupons" :key="item.id">
          <td>{{index + 1}}</td>
          <td>{{item.title}}</td>
          <td>{{item.code}}</td>
          <td>{{item.percent}}</td>
          <td>{{item.due_date}}</td>
          <td>
            <span v-if="item.is_enabled == 1" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button
              class="btn btn-sm btn-outline-primary"
              @click="openModal('#couponModal', 'coupon', 'Edit', item)"
            ></button>
          </td>
          <td>
            <button class="btn btn-sm btn-outline-primary" @click="openModal('#delcouponmodal', 'coupon', 'Del', item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination @updatepage="getcoupons"/>
    <Modal/>
  </div>
</template>

<script>
import Pagination from '../components/Pagination.vue';
import Modal from "../components/Modal.vue";

export default {
  methods: {
      getcoupons(page) {
          this.$store.dispatch("getCoupons", page);
      },
      openModal(modal, species, status, item) {
      this.$store.dispatch("openModal", {modal, species, status, item}); 
    },
  },
  created() {
      this.getcoupons();
  },
  computed: {
      coupons() {
          return this.$store.state.coupons;
      }
  },
  components: {
      Pagination,
      Modal
  }
};
</script>