<template>
  <div>
    <div class="text-right">
      <button type="button" class="btn btn-primary" @click="openModal('#productModal', 'product', 'New', {})"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in adminproducts" :key="item.id">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <td class="text-right">{{item.origin_price | currency}}</td>
          <td class="text-right">{{item.price | currency}}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-sm btn-outline-primary" @click="openModal('#productModal', 'product', 'Edit', item)"></button>
          </td>
          <td>
            <button class="btn btn-sm btn-outline-primary" @click="openModal('#delProductModal', 'product', 'Del', item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination @updatepage="getAdminProducts"/>
    <Modal/>
  </div>
</template>

<script>
import Pagination from "../components/Pagination.vue";
import Modal from "../components/Modal.vue";

export default {
  methods: {
    getAdminProducts(page) {
      this.$store.dispatch("getAdminProducts", page);
    },
    openModal(modal, species, status, item) {
      this.$store.dispatch("openModal", {modal, species, status, item}); 
    },
  },
  created() {
    this.getAdminProducts();
  },
  computed: {
    adminproducts() {
      return this.$store.state.adminproducts;
    },
    error() {
      return this.$store.state.errormessage;
    }
  },
  watch: {
    error: function (value) {
      if (value) {
        this.$router.push('/login');
        this.$store.dispatch('updateerror', false);
      }
    }
  },
  components: {
    Pagination,
    Modal
  }
};
</script>