    },
    addbanners(modal) {
      this.$validator.validate().then(result => {
        if (result) {
          this.$store.dispatch("addBanners", modal);
        } else {
          console.log("");
        }
      });
    },
    delbanners(modal) {
      this.$store.dispatch('delBanners', modal);
    }
  },
